import Recommend from "../recommend/Recommend"
export default {
	data() {
		return {
			list: [],
			action: 0, //tab切换内容
			act:"-2",
			windowHeight:0,
			statusBarHeight:0
		}
	},
	components: {
		Recommend
	},
	methods: {
		tabs(index) {//tab切换
			this.action = index
		},
		change(e) {//改变页面
			this.action = e.detail.current
			this.act = e.detail.current-2
		}
	},
	created() {
		let that = this
		uni.request({ //列表数据
			url: "http://ceshi3.dishait.cn/api/index_category/data",
			success(res) {
				that.list = res.data.data
			}
		})
		this.windowHeight = uni.getSystemInfoSync().windowHeight-90//获取屏幕高度
		uni.getSystemInfo({
			success(res) {
				that.statusBarHeight = res.statusBarHeight+50
			}
		})
	}
}
